<template>
  <div>
    <ik-data-table
      ref="billTable"
      :use-default-action="false"
      :model="model"
      :required-date-value="dates"
      :entity-name="$t('全部账单')"
      style="width: 100%"
      :use-select="false"
      :use-customer-action-only="true"
      :icon="'mdi-format-list-bulleted'"
      :use-date-filter="true"
    >
      <template #footer="{selectItems,items}">
        <div class="d-flex pa-2">
          <template v-for="(n, i) in billSelectionList">
            <v-btn
              :key="i"
              class="mr-1"
              :color="billSelectionColorList[i]"
              dark
              outlined
              small
              @click="setBillDates(i)"
            >
              {{ n }}
            </v-btn>
          </template>

          <v-btn
            class="ml-1"
            small
            color="green"
          >
            {{ $t('sumAmount') }}：€{{ calculateTotalPaymentPrice().toFixed(2) }}
          </v-btn>
          <v-btn
            small
            class="primary"
            @click="print"
          >
            {{ $t('打印') }}
          </v-btn>
        </div>
      </template>
      <template #item.action="{ item }">
        <v-icon
          class="mr-2"
          small
          @click="goNext(item)"
        >
          mdi-arrow-right
        </v-icon>
      </template>
    </ik-data-table>
    <v-dialog
      v-model="showDetail"
      fullscreen
    >
      <v-card color="#f6f6f6">
        <v-toolbar
          dense
        >
          <v-spacer/>
          <v-toolbar-items class="mr-n4">
            <v-btn
              color="error"
              @click="showDetail=false"
            >
              <v-icon
                color="white"
                left
              >
                mdi-close
              </v-icon>
              <span style="font-size: 24px">Zurück</span>
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <bill-detail-page :id="currentOrderId"/>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>

  import { printSummaryBon } from '@/components/Utlis/api'
  import IKUtils from 'innerken-js-utils'
  import HistoryBills from '../../model/HistoryBills'
  import { IkDataTable } from 'metaflow-js'
  // import moment from 'moment'
  import BillDetailPage from '../Details/BillDetailPage'
  import dayjs from 'dayjs'

  export default {
    components: {
      BillDetailPage,
      IkDataTable,
    },
    data: function () {
      return ({

        model: HistoryBills,
        dates: [dayjs().format('YYYY-MM-DD'), dayjs().format('YYYY-MM-DD')],
        timer: null,
        showDetail: null,
        currentOrderId: null,
        menu: false,
        modal: false,
        menu2: false,
        billSelectionList: [
          this.$i18n.t('当日结单'),
          this.$i18n.t('本周结单'),
          this.$i18n.t('本月结单'),
        ],
        billSelectionColorList: ['green darken-2', 'blue darken-2', 'purple darken-2', 'pink'],
      })
    },
    async mounted () {
      this.dates = [dayjs().format('YYYY-MM-DD'), dayjs().format('YYYY-MM-DD')]
    },
    methods: {
      calculateTotalPrice (items) {
        return items ? items.reduce(
          (arr, item) =>
            item.consumeTypeStatusId !== 5 ? arr + parseFloat(item.totalPrice) : arr
          , 0) : 0
      },
      calculateTotalPaymentPrice () {
        const items = this.$refs.billTable.tableItem
        if (!this.$refs.billTable || !this.$refs.billTable.filterItem.paymentArr ||
          (this.$refs.billTable.filterItem.paymentArr.length === 0)) {
          return items ? items.reduce(
            (arr, item) =>
              item.consumeTypeStatusId !== 5 ? arr + parseFloat(item.totalPrice) : arr
            , 0) : 0
        } else {
          const paymentArr = this.$refs.billTable.filterItem.paymentArr
          let totalMoney = 0
          paymentArr.forEach(indexItem => {
            for (const item of items) {
              if (item.payLogs !== null) {
                item.payLogs.forEach(payItem => {
                  if (parseInt(payItem.paymentMethodId) === parseInt(indexItem)) {
                    totalMoney += parseFloat(payItem.amount)
                  }
                })
              }
            }
          })
          return totalMoney
        }
      },
      goNext (item) {
        this.showDetail = true
        this.currentOrderId = item.orderId
      },
      setBillDates (i) {
        switch (i) {
          case 0:
            this.dates = [dayjs().format('YYYY-MM-DD'), dayjs().format('YYYY-MM-DD')]
            break
          case 1:
            this.dates = [dayjs().startOf('week').format('YYYY-MM-DD'), dayjs().format('YYYY-MM-DD')]
            break
          case 2:
            this.dates = [dayjs().startOf('month').format('YYYY-MM-DD'), dayjs().format('YYYY-MM-DD')]
            break
          case 3:
            this.dates = [dayjs().startOf('year').format('YYYY-MM-DD'), dayjs().format('YYYY-MM-DD')]
        }
      },

      print () {
        // console.log('this.dates.', this.dates)
        const timeSpan = { timespan: this.$refs.billTable.okDates.join(' - ') }
        IKUtils.showConfirm('Bist du sicher?', 'Möchten Sie alle Datensätze drucken?', () => {
          printSummaryBon(timeSpan).then(() => {
            IKUtils.toast('Erfolgreich drucken!')
          })
        })
      },

    },
  }
</script>
