import GlobalSettings from '@/GlobalSettings'

import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import ConsumeType from './ConsumeType'
import Payment from './Payment'
import PaymentColors from './PaymentColors'
import Servant from '@/model/Servant'

const entity = {
  orderId: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      disableNew: true,
      disableEdit: true,
      requiredNew: false,
    },
  },
  tableName: {
    tableConfig: {
      sortable: true,
    },
  },
  servantName: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Servant.getList,
        itemText: 'name',
        itemValue: 'name',
      },
    },
    displayName: 'servantName',
  },

  totalPrice: {
    tableConfig: {
      sortable: true,
    },
  },
  payMethod: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Payment.getList,
        itemText: '_langsname',
        color: PaymentColors.getList(),
      },
      merge: false,
    },
    displayName: '付款状态',

  },
  paymentStr: {
    formConfig: {
      required: false,
    },
    displayName: 'payMethod',
  },
  consumeTypeId: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: ConsumeType.getList,
      },
    },
  },
  paymentArr: {
    displayName: 'payMethod',
    type: IKDataEntity.Types.Option,
    header: false,
    formConfig: {
      required: false,
      type: {
        selectItems: Payment.getList,
        // itemText: '_langsname',
        multiple: true,
        disabledItem: (item) => {
          if (item.id === 0) {
            return ({
              ...item,
              disabled: item,
            })
          } else {
            return item
          }
        },
      },
    },
  },
  updateTimestamp: {
    displayName: '结账时间',
  },
}

const config = {
  load: async function (filter) {
    const timespan = filter[0].dateFilter.join(' - ')
    const paymentList = await Payment.getList()
    const list = (await hillo.get('Orders.php', {
      op: 'withSortAndFilter',
      lang: GlobalSettings.getLang(),
      timespan: timespan,
    })).content

    return list.map(function (item) {
      if (item.payLogs) {
        item.paymentStr = []
        item.paymentArr = []
        item.payLogs.forEach(n => {
          n.paymentOriginalItem = paymentList.find(p => p.id === parseInt(n.paymentMethodId))
          if (n.paymentMethodId !== '9') {
            let paymentStr = ''
            if (n.paymentOriginalItem) {
              paymentStr = n.paymentOriginalItem._langsname + ':' + n.amount
            }
            item.paymentStr.push(paymentStr)
            item.paymentArr.push(n.paymentMethodId)
          }
        })
        item.paymentStr = item.paymentStr.join(' / ')
        item.paymentArr = String(item.paymentArr)
      }
      item.id = item.orderId
      return item
    })
  },
}
export default IKDataEntity.ModelFactory(entity, config)
